<template>
  <div class="success-page">
    <h2></h2>
    <div v-if="message">
      {{ message }}
    </div>
    <div v-if="redirectTo">
      <router-link :to="redirectTo"></router-link>
    </div>
  </div>
</template>

<script>

export default {
  data() {
    return {
      message: '',
      redirectTo: null
    };
  },
  created() {
    // 
    this.message = this.$route.query.message || '';
    this.redirectTo = this.$route.query.to || '/';
  }
};
</script>

